@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.material-icons {
  font-family: "Material Icons";
  font-weight: normal;
  font-style: normal;
  font-size: 24px; /* Preferred icon size */
  width: 24px;
  display: inline-block;
  line-height: 1;
  text-transform: none;
  letter-spacing: normal;
  word-wrap: normal;
  white-space: nowrap;
  direction: ltr;
  vertical-align: middle;

  /* Support for all WebKit browsers. */
  -webkit-font-smoothing: antialiased;
  /* Support for Safari and Chrome. */
  text-rendering: optimizeLegibility;

  /* Support for Firefox. */
  -moz-osx-font-smoothing: grayscale;

  /* Support for IE. */
  font-feature-settings: "liga";
}

.login-container {
  padding: 50px 20px;
}

.login-title {
  text-align: center;
}

.invalid-alert-visible {
  visibility: visible;
}

.invalid-alert {
  visibility: hidden;
}

.error-message {
  color: #a94442;
  margin-top: 2px;
  font-size: 12px;
}
  
.home-nav {
    padding: 10px 20px;
}

.navbar-dark {
    border-radius: 0px;
    background-color: #34383b!important;
    margin: 0;
}

.navbar-dark .navbar-nav .nav-link {
    color: white;
    opacity: 0.6;
    vertical-align: middle;
    text-decoration: none;
    margin: 0px 15px;
}

.navbar-dark .navbar-nav .active {
    opacity: 0.9;
}

.navbar-dark .navbar-nav .nav-link:hover {
    opacity: 0.8;
}

.material-icons {
    font-size: 17px;
}

.new-leave-container .text-right .btn {
    margin: 20px;
}

.dashboard-container {
    min-height: 300px;
    margin-top: 70px;
    font-size: 14px;
}

.dashboard-container .row>div>label {
    width: 210px;
}

.existing-leaves-container, .pending-leaves-container, .approved-leaves-container {
    margin-top: 50px;
    min-height: 350px;
}

.table>tbody>tr>td{
    vertical-align: middle;
}

.table>tbody>tr>th{
    vertical-align: middle;
}
